function useElasticCollision(A, B) {
    let contact = GJK.bind(this)(A, B);
    if (contact) {
        contact.draw(this.ct);
        contactHandler(contact)
    }
    // contact && contact.draw(this.ct);
    // console.log(contact);
}

export {
    useElasticCollision
}
export default useElasticCollision;



